.about{
    color:white;
    padding-top: 76px;
    .aboutH{
        text-align: center;
        font-size: 2.8em;
        margin-bottom: 0;
        letter-spacing: 5px;
    }
}

.aboutTop{
    // padding: 76px 0 0 0;
    min-height: 40vh;
    width: 90%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    
    border-bottom: solid 5px rgb(179, 12, 46);

    .text, .intro2text{
        word-wrap: break-word;
    }

    .intro2{
        font-size: 0.5em;
        display: flex;
        align-items: center;

        .hello{
            font-size: 2.8em;
        }
    }

    .intro,.intro2{
        min-height: 250px;
        max-width: 60vw;
        min-width: 40%;
        margin: 20px 10px;
    }

    .intro{
        display: flex;
        align-items: center;
        flex-direction: column; 
        justify-content: center;
    }

    .photoName{
        display: flex;
        align-items: center;
        margin: 0 0 20px 0;
    }

    .text{
        margin-left: 25px;
        font-size: 0.5em;
        // display: flex;
        // align-items: center;
        // // justify-content: center;

        .quote{
            padding-bottom: 20px;
        }
    }

    .intro2text{
        margin-left: 25px;
    }

    .photo{
        height: 70px;
        border-radius: 50%;
        margin-right: 10px;
    }

    .name{
        font-size: 30px;
    }

    @media only screen and (max-width: 980px){
        .intro, .intro2{
            min-height: 250px;
            max-width: 100vw;
            min-width: 40%;
            margin: 20px 10px;

        }
    }
}



.skills{

    width: 90%;
    margin: 0 auto;
    min-height: 60vh;

    .header{
        text-align: center;
        font-size: 2.8em;
        letter-spacing: 5px;
    }

    .logos{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }
}