
.navBar{
    position: fixed;
    width: 90%;
    padding-top: 5px;
    margin: 0 auto;
    left: 0;
    right: 0;

    display: flex;
    align-items: center;
    justify-content: space-between;

    border-bottom: solid 5px rgb(179, 12, 46);
    border-radius: 0 0 25px 25px;
    background-color: black;

    #hold{
        background-color:rgb(179, 12, 46); 
    }

    .logo{

        border-radius: 30px 30px 0 0;
        padding: 0 5px;
    }

    .logo img{
        width: 100px;
        cursor: pointer;  
    }

    .logo:hover{
        background-color: rgb(179, 12, 46);
        transition: 0.3s all;
    }

    .item{
        cursor: pointer;
        // margin: 0 10px;
        padding: 10px 15px 15px 15px;
        margin: 0 10px;
        padding-bottom: 20px; 
        font-size: 1.2em;
        border-radius: 30px 30px 0 0;
    }

    .item:hover{
        background-color: #b30c2e;
        transition: 0.3s all;
    }

    .pad{
        margin: 0px 50px;
    }

    .logo{
        margin-right: 0;
    }

    a{
        text-decoration: none;
        color:white;
    }

    @media only screen and (max-width: 730px){
        .item{
            font-size: 1em;
            margin: 0;
        }

        @media only screen and (max-width: 600px){
            .pad{
                margin: 0 20px;
            }
        }
    }
}


@media only screen and (max-width: 480px){
    .navBar{
        flex-wrap: wrap;
        justify-content: center;
    
        .item{
            padding: 5px 5px 5px 5px;
            margin-top: 5px;
        }

        .logo{
            padding: 0 15px;
            border-radius: 30px 30px 30px 30px;
            margin-bottom: 10px;
        }
    }
}