.project{
    min-height: 450px;
    width: 450px;
    margin: 20px;
    // flex:1;
    
    a{
        text-decoration: none;
        color: white;
    }

    .content{
        width: 90%;
        height: 450px;
        padding: 10px;
        margin: 0 auto;
        border-right:solid 5px rgb(179, 12, 46);
        border-left:solid 5px rgb(179, 12, 46);
        border-top:solid 1px rgb(179, 12, 46);
        border-bottom:solid 1px rgb(179, 12, 46);
        border-radius: 30px;
        color: white;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        
        .project_name{
            text-align: center;
            font-size: 2em;
            margin:0;
            margin-bottom: 15px;
        }

        .about_project{
            margin: 0 auto;
            text-align: justify;
            font-size: 1.2em;
        }

        img{
            width: 100%;
            margin: 10px 0 20px 0;
            border-radius: 20px;
        }
    }

    @media only screen and (max-width: 440px){
        .content{
            min-height: 500px;
            font-size: 0.8em;
        }
    }

    .content:hover{
        transition: 0.3s all;
        transform: scale(1.05);
    }
}

@media only screen and (max-width: 490px){
    .project{
        margin-top: 50px;
    }
}

