.skill{

    margin: 10px;

    .skill-icon{
        height: 100px;
        width: 100px;
        background-color: white;
        padding: 30px;
        border-radius: 50%;
    }

    .skill-icon:hover{

        img{
            transition: 0.3s all;
        transform: scale(1.2);
        }
        background-color: grey;
        transition: 0.3s all;
        transform: scale(1.1);
    }

    img{
        height: 100%;
        width: 100%;
        border-radius: 10px;
    }
}