
.contact{
    // height: 90vh;
    padding-top: 76px;
    // display: flex;
    // justify-content: center;
    // align-items: center;
    color:white;
    width: 90%;
    margin: 0 auto;
    
    h1{
        text-align: center;
        font-size: 2.8em;
        margin-bottom: 0;
        margin-top:25px;
        letter-spacing: 5px;
    }

    .content{
        height: 50vh;
    // padding-top: 76px;
        display: flex;
        justify-content: center;
        align-items: center;
        // color:white;
        display: flex;
        justify-content: center;
        align-items: center;
        border-bottom: solid 5px rgb(179, 12, 46);
    }

    .form{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        min-height: 60vh;
    

        form{

            text-align: center;

            fieldset{
                display: flex;
                flex-direction: column;
                border:none;
                text-align: left;

                label{
                    margin: 10px;
                }

                input, textarea{
                    padding: 10px 30px;
                    border: solid 2px rgb(179, 12, 46);
                }

                input, textarea{
                    width: 40vw;
                }
            }

            .submit{
                padding: 20px 50px;
                margin-top: 20px;
                background-color: rgb(179, 12, 46);
                border:none;
                font-family: 'Roboto Mono', monospace;
                font-size: 1.2em;
                border-radius: 50px;
                font-weight: 900;
                color:white;
                cursor: pointer;
            }

            .submit:hover{
                transition: 0.3s all;
                padding-left:80px;
                padding-right:80px;
                background-color: white;
                color:rgb(179, 12, 46);
            }
        }
    }
}